.container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  transition: transform 0.5s ease;
}

.container.active {
  transform: translateX(100%);
}

.form {
  display: flex;
  flex-direction: column;
  width: 300px;
  padding: 20px;
  border: 1px solid #ccc;
  border-radius: 5px;
  background-color: #fff;
}

.form input {
  margin-bottom: 15px;
  padding: 10px;
  border: 1px solid #ccc;
  border-radius: 5px;
}

.form button {
  padding: 10px;
  background-color: #007bff;
  color: white;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}

.form button:hover {
  background-color: #0056b3;
}

.toggle-password {
  cursor: pointer;
  background: none;
  border: none;
  color: #007bff;
  font-size: 14px;
}

.error-popup {
  display: none;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: rgba(0, 0, 0, 0.5);
  justify-content: center;
  align-items: center;
}

.error-popup.active {
  display: flex;
}

.error-popup .popup-content {
  background-color: white;
  padding: 20px;
  border-radius: 5px;
  text-align: center;
}

.error-popup .close-btn {
  cursor: pointer;
  color: #007bff;
  margin-top: 10px;
}